.button {
    box-sizing: border-box;
    display: block;
    text-decoration: none;
    text-align: center;
    color: white;
    background-color: $main-red;
    padding: 15px;
    width: 100%;
    transition: background-color 0.25s;
  
    &:hover {
      background-color: #ae1c23;
    }
  
    &:active {
      background-color: #eb343d;
    }
  }