.page {
    font-family: "Cinzel", serif;
  
    &__section {
      margin-bottom: 100px;
    }
  
    &__section-title {
      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      text-align: center;
      width: 100%;
  
      @include onMedium {
        font-size: 48px;
        line-height: 48px;
      }
  
      @include onBig {
        font-size: 48px;
        line-height: 48px;
      }
    }
  
    &__menu {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
  
      transform: translateX(-100%);
      transition: transform 0.5s ease-in-out;
  
      &:target {
        transform: translateX(0);
      }
    }
  }