.top-bar {
    height: max-content;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 0;
  
    @include onMedium {
      padding: 50px 0;
    }
  
    @include onBig {
      padding: 50px 0;
    }
  
    &__title {
      display: inline-block;
      color: white;
      line-height: 28px;
      font-weight: 700;
      font-size: 40px;
    }
  
    &__container {
      display: flex;
      gap: 30px;
  
      @include onMedium {
        gap: 63px;
      }
  
      @include onBig {
        gap: 63px;
      }
    }
  }