.form-input {
    font-family: inherit;
    box-sizing: border-box;
    width: 100%;
    padding: 14px 10px;
  
    background-color: #f8f8fa;
    border: 1px solid transparent;
  
    &::placeholder {
     
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: #7e7e83;
    }
  
    &:hover {
      border-color: #d5d7de;
    }
  
    &:focus {
      border-color: #161616;
    }
  
    &--textarea {
      resize: none;
      height: 144px;
    }
  }